import React from 'react';

const GraphLegend = (props) => {
    return(
        <div className="graph__legend">
            <div class="legend__wrapper" className={props.className}>
                <p className="legend__label"><span>●</span>{props.legendText}</p>
                <p className="legend__value">{props.legendValue}</p>
            </div>
        </div>
    )
}

export default GraphLegend;    